@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@400;500;600&display=swap");
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: "Barlow Semi Condensed", sans-serif;
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 1.8rem;
  color: #fff;
  background: #f2f2f2;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --violet: hsl(263, 55%, 52%);
  --dark-gray-blue: hsl(217, 19%, 35%);
  --dark-black-blue: hsl(219, 29%, 14%);
  --white: hsl(0, 0%, 100%);
  --light-violet: hsl(264, 82%, 70%);
  --light-gray: hsl(0, 0%, 81%);
  --light-gray-blue: hsl(218, 5%, 57%);
  --light2-gray-blue: hsl(210, 46%, 95%);
}

/*
  0 - 600px:       Phone
  600 - 900px:     Tablet portrait
  900 - 1200px:    Tablet landscape
  [1200 - 1800] is where our normal styles apply. > Desktop frst aprouch
  1800px + :       Big desktop
*/
/*
 Example:

h1 {
  font-size: rem(33.536);
}

// In CSS:
h1 {
  font-size: 3.3536rem;
}

*/
.border-purple {
  border: 2px solid var(--light-violet);
}

.grid-item {
  grid-column: 1/span 2;
}
@media (max-width: 41.6875em) {
  .grid-item {
    grid-column: 1;
    grid-row: 1/2;
  }
}
@media (max-width: 23.4375em) {
  .grid-item {
    grid-column: 1;
    grid-row: 1/2;
  }
}

.grid-item-2 {
  grid-column: 2/span 2;
}
@media (max-width: 41.6875em) {
  .grid-item-2 {
    grid-column: 1;
    grid-row: 4/span 2;
  }
}
@media (max-width: 23.4375em) {
  .grid-item-2 {
    grid-column: 1;
    grid-row: 4/span 2;
  }
}

.grid-item-3 {
  grid-column: 4/5;
  grid-row: 1/span 2;
}
@media (max-width: 41.6875em) {
  .grid-item-3 {
    grid-column: 1;
    grid-row: 6/span 2;
  }
}
@media (max-width: 23.4375em) {
  .grid-item-3 {
    grid-column: 1;
    grid-row: 6/span 2;
  }
}

.bg-purple {
  background: var(--violet);
}

.bg-gray-blue {
  background: var(--dark-gray-blue);
}

.bg-black-blue {
  background: var(--dark-black-blue);
}

.bg-white {
  background: var(--white);
}

.section-testimonials {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 41.6875em) {
  .section-testimonials {
    padding: 7.5rem 2.4rem;
  }
}
@media (max-width: 23.4375em) {
  .section-testimonials {
    padding: 7.1rem 2.4rem;
  }
}

.testimonials-container {
  max-width: 111rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 3rem;
  row-gap: 2.4rem;
}
@media (max-width: 41.6875em) {
  .testimonials-container {
    min-height: 18.24rem;
    grid-template-columns: 1fr;
    column-gap: 0;
  }
}
@media (max-width: 23.4375em) {
  .testimonials-container {
    min-height: 18.24rem;
    grid-template-columns: 1fr;
    column-gap: 0;
  }
}

.testimonial {
  border-radius: 8px;
  box-shadow: 40px 60px 50px -47px rgba(72, 85, 106, 0.247378);
  padding: 2.6rem 3.2rem;
  position: relative;
  z-index: 999;
}
.testimonial--quote::after {
  content: url(/images/bg-pattern-quotation.svg);
  position: absolute;
  top: 0;
  right: 8rem;
  font-size: 10.4rem;
  color: var(--light-violet);
  z-index: -100;
}
@media (max-width: 41.6875em) {
  .testimonial--quote::after {
    right: 2.4rem;
  }
}
@media (max-width: 23.4375em) {
  .testimonial--quote::after {
    right: 2.4rem;
  }
}
.testimonial__person {
  display: flex;
  gap: 1.7rem;
}
.testimonial__img {
  width: 2.8rem;
  height: 2.8rem;
  object-fit: cover;
  border-radius: 50%;
}
.testimonial__name {
  font-size: 1.3rem;
  line-height: 1.3rem;
}
.testimonial__name--gray-blue {
  color: var(--dark-gray-blue);
}
.testimonial__graduate {
  font-size: 1.1rem;
  line-height: 1.1rem;
  margin-top: 0.3rem;
  color: var(--light-gray);
}
.testimonial__graduate--light-blue {
  color: var(--light-gray-blue);
}
.testimonial__heading {
  font-size: 2rem;
  font-weight: 600;
  line-height: 2.4rem;
  margin-top: 1.8rem;
}
.testimonial__heading--dark-gray {
  color: var(--dark-gray-blue);
}
.testimonial__heading--gray-light {
  color: var(--light2-gray-blue);
}
.testimonial__heading--light-gray-blue {
  color: var(--light2-gray-blue);
}
.testimonial:first-child .testimonial__text, .testimonial:nth-child(2) .testimonial__text {
  margin-top: 1.6rem;
}
@media (max-width: 23.4375em) {
  .testimonial:first-child .testimonial__text {
    margin-top: 4rem;
    margin-right: 1.6rem;
  }
}
.testimonial__text {
  font-size: 1.3rem;
  margin-top: 2.4rem;
}
.testimonial__text--gray {
  color: var(--light-gray);
}
.testimonial__text--light-gray-blue {
  color: var(--light-gray-blue);
}